import React from 'react';
import styled from 'styled-components';
import avatar from '../../assets/avatar.png';
import { menuItems } from '../../utils/menuItems';
import { signout } from '../../utils/Icons';
import { useNavigate } from 'react-router-dom';

function Navigation({ active, setActive, isAuthenticated, onSignOut }) {
    const navigate = useNavigate();

    const handleSignOutClick = () => {
        onSignOut(); // Call the sign-out function passed as a prop
    };

    return (
        <NavStyled>
            {isAuthenticated ? (
                <div className='user-con'>
                    <img src={avatar} alt='' />
                    <div className='text'>
                        <h2>User</h2>
                        <p>Your Money</p>
                    </div>
                </div>
            ) : (
                <div className='auth-buttons'>
                    <AuthButton primary onClick={() => navigate('/signin')}>
                        Login
                    </AuthButton>
                    <AuthButton onClick={() => navigate('/signup')}>
                        Sign Up
                    </AuthButton>
                </div>
            )}
            <ul className="menu-items">
                {menuItems.map((item) => {
                    return (
                        <li
                            key={item.id}
                            onClick={() => setActive(item.id)}
                            className={active === item.id ? 'active' : ''}
                        >
                            {item.icon}
                            <span>{item.title}</span>
                        </li>
                    );
                })}
            </ul>
            {isAuthenticated && (
                <div className="bottom-nav">
                    <button onClick={handleSignOutClick}>
                        {signout}Sign Out
                    </button>
                </div>
            )}
        </NavStyled>
    );
}
const AuthButton = styled.button`
    background-color: ${({ primary }) => (primary ? '#4CAF50' : 'transparent')};
    color: ${({ primary }) => (primary ? '#fff' : 'black')};
    padding: 0.75rem 1.5rem;
    border: ${({ primary }) => (primary ? 'none' : '1px solid #ccc')};
    border-radius: 8px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: ${({ primary }) =>
            primary ? '#45a049' : 'rgba(0, 0, 0, 0.1)'};
    }
`;

const NavStyled = styled.nav`
    padding: 2rem 1.5rem;
    width: 374px;
    height: 100%;
    background: rgba(252, 246, 249, 0.78);
    border: 3px solid #FFFFFF;
    backdrop-filter: blur(4.5px);
    border-radius: 32px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 2rem;
    
    .user-con {
        height: 100px;
        display: flex;
        align-items: center;
        gap: 1rem;
        img {
            width: 80px;
            height: 80px;
            border-radius: 50%;
            object-fit: cover;
            background: #fcf6f9;
            border: 2px solid #FFFFFF;
            padding: .2rem;
            box-shadow: 0px 1px 17px rgba(0, 0, 0, 0.06);
        }
        h2 {
            color: rgba(34, 34, 96, 1);
        }
        p {
            color: rgba(34, 34, 96, .6);
        }
    }

    .auth-buttons {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }

    .menu-items {
        flex: 1;
        display: flex;
        flex-direction: column;
        li {
            display: grid;
            grid-template-columns: 40px auto;
            align-items: center;
            margin: .6rem 0;
            font-weight: 500;
            cursor: pointer;
            transition: all .4s ease-in-out;
            color: rgba(34, 34, 96, .6);
            padding-left: 1rem;
            position: relative;
            i {
                color: rgba(34, 34, 96, 0.6);
                font-size: 1.4rem;
                transition: all .4s ease-in-out;
            }
        }
    }

    .active {
        color: rgba(34, 34, 96, 1) !important;
        i {
            color: rgba(34, 34, 96, 1) !important;
        }
        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 4px;
            height: 100%;
            background: #222260;
            border-radius: 0 10px 10px 0;
        }
    }

    .bottom-nav {
        display: flex;
        margin-top: 2rem;
        button {
            background-color: transparent;
            color: black;
            padding: 0.75rem 1.5rem;
            border: none;
            border-radius: 8px;
            font-size: 1rem;
            cursor: pointer;
            transition: background-color 0.3s ease;
            display: flex;
            align-items: center;
            gap: 0.5rem;
            
            &:hover {
                background-color: rgba(0, 0, 0, 0.1);
            }

            i {
                font-size: 1.2rem;
            }
        }
    }
`;

export default Navigation;
